<template>
  <div class="common-layout">
    <el-container>
      <el-header>
        <el-menu
          class="menu"
          :default-active="activeIndex"
          mode="horizontal"
          @select="handleSelect"
        >
          <el-menu-item
            v-for="route in routes"
            :key="route.path"
            :index="route.path"
            class="menu-item"
          >
            {{ route.name }}
          </el-menu-item>
        </el-menu>
      </el-header>
      <el-main>
        <router-view />
      </el-main>
      <el-footer>
        <Bottom :ref="'Bottom'" />
      </el-footer>
    </el-container>
  </div>
</template>

<script>
import { useRouter, useRoute } from 'vue-router';
import Bottom from '@/components/bottom.vue';

export default {
  name: 'MyHome',
  components: {
    Bottom, // 注册 Bottom 组件
  },
  setup() {
    const router = useRouter(); // 获取路由实例
    const route = useRoute();
    const routes = [
      { path: '/home', name: '首页' },
      { path: '/products', name: '产品中心' },
      { path: '/about', name: '关于我们' },
      { path: '/callUs', name: '联系我们' }
      // 可以继续添加更多路由
    ];
    const handleSelect = (path) => {
      router.push(path); // 路由跳转
    };

    const activeIndex = route.path; // 当前路由路径作为默认激活项

    return {
      routes,
      handleSelect,
      activeIndex,
    };
  },
};
</script>

<style>
.menu {
  font-size: 18px;
  justify-content: space-between;
  background: #fff;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
}

.menu-item {
  position: relative;
  padding: 10px 20px;
  text-align: center;
  cursor: pointer;
  transition: all 0.3s ease;
}

.menu-item:hover::after {
  content: '';
  position: absolute;
  left: 10%;
  bottom: -5px;
  width: 80%; /* 阴影占据 80% 的宽度 */
  height: 4px;
  background: #409eff; /* 阴影颜色 */
  box-shadow: 0 4px 10px rgba(64, 158, 255, 0.3);
  transition: all 0.3s ease;
}
</style>
